.login-container {
  margin: 30px auto;
  max-width: 400px;        /* compact width */
  background: rgba(255,255,255,0.95);
  padding: 30px 25px;      /* reduce vertical padding */
  border-radius: 20px;
  box-shadow: 0 10px 25px rgba(0,0,0,0.15);
  text-align: center;
}

.login-form h2 {
  margin-bottom: 20px;     /* smaller gap above inputs */
  color: #333;
}

.login-form input {
  width: 100%;
  padding: 10px 12px;      /* smaller input height */
  margin-bottom: 15px;
  border-radius: 10px;
  border: 1px solid #ccc;
  font-size: 15px;
  outline: none;
}

.login-form input:focus {
  border-color: #007bff;
  box-shadow: 0 0 6px rgba(0,123,255,0.3);
}

.login-form button {
  padding: 10px 0;         /* reduce button height */
  width: 100%;
  border: none;
  border-radius: 10px;
  background: #007bff;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  transition: background 0.3s, transform 0.2s;
}

.login-form button:hover {
  background: #0056b3;
  transform: translateY(-1px);
}

.error {
  color: #e74c3c;
  margin-bottom: 10px;
  font-size: 14px;
}
