/* Professional background for dashboard */
.admin-container {
  width: 90%;
  max-width: 900px;
  margin: 50px auto;
  background: rgba(255,255,255,0.95);
  padding: 30px;
  border-radius: 20px;
  box-shadow: 0 10px 25px rgba(0,0,0,0.2);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* Dark mode optional */
.admin-container.dark {
  background: #1e3c72;
  color: #ecf0f1;
}

/* Header */
.admin-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.logout-btn, .theme-btn {
  padding: 10px 18px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  margin-left: 10px;
}

.logout-btn { background: #e74c3c; color: white; }
.logout-btn:hover { background: #c0392b; }

.theme-btn { background: #1e3c72; color: white; }
.theme-btn:hover { background: #16325c; }

/* Search */
.search-container input {
  width: 100%;
  padding: 12px;
  border-radius: 10px;
  border: 1px solid #ccc;
  margin-bottom: 25px;
  outline: none;
}

.search-container input:focus {
  border-color: #1e3c72;
  box-shadow: 0 0 6px rgba(30,60,114,0.4);
}

/* Student cards grid */
.admin-student-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

/* Individual student card */
.admin-student-card {
  background: #fff;
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 5px 18px rgba(0,0,0,0.1);
  transition: transform 0.3s, box-shadow 0.3s;
}

.admin-student-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0,0,0,0.15);
}

/* Dark mode card */
.admin-container.dark .admin-student-card {
  background: #2a5298;
  color: #ecf0f1;
}

/* Card text */
.admin-student-card p {
  margin: 5px 0;
}
