/* Professional background */
body {
  background: linear-gradient(to right, #1e3c72, #2a5298); /* professional gradient */
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  min-height: 100vh;
}

/* Container */
.container {
  background: rgba(255,255,255,0.95);
  padding: 40px;
  border-radius: 20px;
  max-width: 450px;
  margin: 50px auto;
  box-shadow: 0 10px 30px rgba(0,0,0,0.2);
  text-align: center;
}

/* Heading */
h1 {
  color: #1e3c72;
  margin-bottom: 25px;
}

/* Form styling */
.student-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.student-form input, 
.student-form select {
  padding: 12px 15px;
  border-radius: 10px;
  border: 1px solid #ccc;
  font-size: 15px;
  outline: none;
  transition: border 0.3s, box-shadow 0.3s;
}

.student-form input:focus,
.student-form select:focus {
  border-color: #1e3c72;
  box-shadow: 0 0 6px rgba(30,60,114,0.4);
}

.student-form button {
  padding: 12px;
  border: none;
  border-radius: 10px;
  background: #1e3c72;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  transition: background 0.3s, transform 0.2s;
}

.student-form button:hover {
  background: #16325c;
  transform: translateY(-2px);
}

.error {
  color: #e74c3c;
}

.success {
  color: #2ecc71;
}
